import React, {Component,useEffect,useState} from "react"
import "./style_home_galary.scss"
import logo from "./images/home/title-shap.png"
const obj={//this is a test object ,remove it and get object using lifecycle
 nav_content:["Christmas","Facilities","Other"],//content to nav bar
 main:[ //content should be in following form
    {
      festival: "Christmas",
      name: "Christmas",
      image: "https://images.pexels.com/photos/1556678/pexels-photo-1556678.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
    },
    {
      festival: "Christmas",
      name: "Christmas Celebration",
      image: "https://images.pexels.com/photos/5779173/pexels-photo-5779173.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
    },
    {
      festival: "Other",
      name: "Seminar",
      image: "https://images.pexels.com/photos/1708988/pexels-photo-1708988.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
    },
    {
      festival: "Facilities",
      name: "Classes",
      image: "https://images.pexels.com/photos/6602623/pexels-photo-6602623.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
    },
    {
      festival: "Facilities",
      name: "other",
      image: "https://images.pexels.com/photos/7418978/pexels-photo-7418978.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
    },
    {
      festival: "Other",
      name: "other pics",
      image: "https://images.pexels.com/photos/5785714/pexels-photo-5785714.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
    }
]
}
function Fnav (props){
   
  return(
      <div className="featured_nav">
           {props.name.map(nam => (
                <a href="javascript:void(0)" id={nam}  onClick={(event)=>props.handlenavClick(event)}>
                  {nam}
                </a>
              ))}
      </div>
  )
}
const Grid = (whatToRender) => {
  const renderGrid = () => {
    if (whatToRender.current_nav === "All") { // use === over == because that will check for type equality as well.
      return whatToRender.arr.map((item) => ( // () is equivalent to return () when looping.
        /* Always pass a unique key if you are creating lists or lopping */
        <div className="featured_grid col-sm"  key={item.name}> 
          <img src={item.image} alt={item.name} className="libimage"/>
          <a className="grid_a" >
            <h3>{item.name}</h3>
          </a>
        </div>
      ))
    }
    for(let i=0;i<whatToRender.navNames.length;i++){
    if (whatToRender.current_nav === whatToRender.navNames[i]) {
      return whatToRender.arr.map((item) => {
        if (item.festival === whatToRender.navNames[i]) {
          return (
            <div className="featured_grid col-sm-6"  key={item.name}> 
            <img src={item.image} alt={item.name} />
            <a className="grid_a" >
              <h3>{item.name}</h3>
            </a>
          </div>
          )
        }
      })
    }
  }
  // Return an empty div if none of the cases pass. So, you return valid JSX
    return <div></div>
  }
  return renderGrid() // Finally return your custom render function
}

class Galary extends Component {
    constructor(props) {
        super(props);
        this.state={
            navNo:1,//no of nav bar
            navNames:["All"],//nav name at start
            current_nav:"All"//current active one
        };

this.handlenavClick=this.handlenavClick.bind(this)
    }
componentDidMount(event){//assigning nav bar at start
        if(this.state.navNo===1){
            let k=obj.nav_content;
            

            this.setState((state)=>({
                navNo:state.navNo+(obj.nav_content.length),
                navNames:state.navNames.concat(k)
            })
            )
           
        }
      
      console.log("this state is now"+JSON.stringify(this.state))
    }
    handlenavClick(event){// changing the current nav and its color on click
        console.log("Clicked event"+event.target.id)
        this.setState({
            current_nav:event.target.id
        })
        
        this.state.navNames.forEach((item)=>document.getElementById(item).style.color="#848484")
        event.target.style.color="green"
        console.log("this state is now"+JSON.stringify(this.state))
    }
    render(){

        return(
    <div className='galary'>
       
            <div className="title-head">Featured <span>Gallery</span><br />
           <h1 className='sub_head'>Recent Programs &amp; Activities</h1> </div>
        <div className="featured-icon" ><img className="icon" src={logo}></img></div>
        <Fnav no={this.state.navNo} handlenavClick={this.handlenavClick} name={this.state.navNames}   />
        <div className="featured_grid_container container-fluid">
          <Grid arr={obj.main} current_nav={this.state.current_nav} navNames={this.state.navNames} />
        </div>
     </div>)
    }
}
export default Galary