
import React,{Component} from 'react';
import { Image } from 'react-bootstrap';
import "./style_log.scss"
class Login extends Component{
  constructor(props){
    super(props)
    this.state ={
        member: this.props.member
    }
  }
  componentDidUpdate() {
      if(this.props.member!=this.state.member){
          this.setState({
              member:this.props.member
          })

      }
}
render(){
return(
      <div className='login' >
  
{/* <!--start login section--> */}



<div className="zlogn">
    <div className="zloginbox">
        <img className="avatar"/>
          <h1 className="h1-font ">{this.state.member} Login</h1>
          <form>
              <p className="text-p">Username</p>
              <input type="text" name="" placeholder="Enter Username " className ='in'/>
              <p className="text-p">Password</p>
              <input type="password" name="" placeholder="Enter Password" className='in'/>
             <button type="button"><a href="ac1.html"><h6>Login</h6></a></button>
              <a href="#">Lost your password?</a><br/>
              <a href="#">Don't have an account?</a>
          </form>
          
      </div>
  
  </div>


{/* <!--end of login section--> */}


 </div>
    )
}
}

export default Login